.button--play {
  -webkit-transition: all 120ms ease;
  -o-transition: all 120ms ease;
  transition: all 120ms ease;
  -webkit-box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.34);
          box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.34);
  height: 60px;
  width: 60px;
  right: 25px;
  bottom: 25px;
  border: 0px;
  position: absolute;
  border-radius: 30px;
  font-size: 28px;
  text-align: center;
  opacity: 0.8;
  background-color: var(--link-color);
  color: #fff;
  cursor: pointer;
}

.dark .button--play {
  -webkit-box-shadow: 0px 0px 2px 0px rgba(255, 255, 255, 0.34);
          box-shadow: 0px 0px 2px 0px rgba(255, 255, 255, 0.34);
}

.button--play.played {
  border-radius: 4px;
}

.button--play:active {
  -webkit-transform: scale(0.86);
      -ms-transform: scale(0.86);
          transform: scale(0.86);
}

.button--play:hover {
  opacity: 1;
}

@media (max-width: 600px) {
  .button--play {
    height: 40px;
    width: 40px;
    font-size: 20px;
    right: 10px;
    bottom: 10px;
  }
}



