.spinner {
  width: 50px;
  height: 50px;
  position: relative;
  margin: 100px auto;
}

.spinner__bounce--1,
.spinner__bounce--2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: var(--link-color);
  opacity: 0.6;
  position: absolute;
  top: 0px;
  left: 0px;
  -webkit-animation: bounce 1.4s infinite ease-in-out;
          animation: bounce 1.4s infinite ease-in-out;
}

.spinner__bounce--2 {
  -webkit-animation-delay: -0.7s;
          animation-delay: -0.7s;
}

@-webkit-keyframes bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



