.list__item--song {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 6px 3px;
  border-bottom: 1px solid var(--border-light);
  overflow: hidden;
}

.list__item--song > div {
  overflow: hidden;
}

.dark .list__item--song {
  border-bottom: 1px solid var(--border-dark);
}

.list__item--song .artist {
  font-size: 11px;
}

.list__link--song {
  font-weight: 600;
}



